'use client';
import { useState, useEffect, useMemo } from 'react';
import { format, isSameDay, isFuture } from 'date-fns';
import { Calendar, Clock, Edit, Save, Trash2, Sparkles, Loader2, X } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from '@/components/ui/dialog';
import { Card, CardContent } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Tooltip, TooltipProvider, TooltipTrigger, TooltipContent } from '@/components/ui/tooltip';
import type { Event, Role, Volunteer, UniversalRole, VolunteerAvailability } from '@/types';
import { useToast } from '@/hooks/use-toast';
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle, AlertDialogTrigger } from '@/components/ui/alert-dialog';
import { arrayRemove, arrayUnion, collection, doc, query, updateDoc, where, writeBatch, getDocs, getDoc } from 'firebase/firestore';
import { useAuth } from '@/hooks/use-auth';
import { useFirebase, useMemoFirebase } from '@/firebase';
import { useCollection } from '@/firebase/hooks/use-collection';
import { useDoc } from '@/firebase/hooks/use-doc';
import { errorEmitter } from '@/firebase/error-emitter';
import { FirestorePermissionError } from '@/firebase/errors';

interface EventDetailsModalProps {
  event: Event;
  isOpen: boolean;
  onClose: () => void;
}

const statusColor: Record<Role['status'], string> = {
  Pending: 'bg-accent',
  Confirmed: 'bg-primary',
  Declined: 'bg-destructive',
};

function RoleRow({ role, onUpdate, onDelete, onAssign, volunteers, isSaving }: { role: Role; onUpdate: (roleName: string) => void; onDelete: () => void; onAssign: (volunteerId: string) => void; volunteers: Volunteer[]; isSaving: boolean }) {
  const [isEditing, setIsEditing] = useState(false);
  const [editedName, setEditedName] = useState(role.roleName);

  const handleSave = () => {
    onUpdate(editedName);
    setIsEditing(false);
  };

  return (
    <div className="flex items-center gap-2 p-2 rounded-md hover:bg-muted/50">
      <TooltipProvider>
        <Tooltip>
          <TooltipTrigger>
            <div className={`h-2.5 w-2.5 rounded-full ${statusColor[role.status]}`} />
          </TooltipTrigger>
          <TooltipContent>{role.status}</TooltipContent>
        </Tooltip>
      </TooltipProvider>

      <div className="flex-1">
        {isEditing ? (
          <Input value={editedName} onChange={(e) => setEditedName(e.target.value)} className="h-8" />
        ) : (
          <p className="font-medium">{role.roleName}</p>
        )}
      </div>
      <div className="w-48">
        <Select
          value={role.assignedVolunteerId ?? ''}
          onValueChange={onAssign}
          disabled={isSaving}
        >
          <SelectTrigger className="h-8">
            <SelectValue placeholder="Assign volunteer..." />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="unassign">
              <span className="text-muted-foreground italic">Unassign</span>
            </SelectItem>
            {volunteers.map(v => (
              <SelectItem key={v.id} value={v.id}>{v.name}</SelectItem>
            ))}
          </SelectContent>
        </Select>
      </div>
      <div className="flex items-center gap-1">
        {isEditing ? (
          <Button size="icon" variant="ghost" className="h-8 w-8" onClick={handleSave} disabled={isSaving}>
            {isSaving ? <Loader2 className="h-4 w-4 animate-spin" /> : <Save className="h-4 w-4" />}
          </Button>
        ) : (
          <Button size="icon" variant="ghost" className="h-8 w-8" onClick={() => setIsEditing(true)}>
            <Edit className="h-4 w-4" />
          </Button>
        )}
        <AlertDialog>
          <AlertDialogTrigger asChild>
            <Button size="icon" variant="ghost" className="h-8 w-8 text-destructive hover:text-destructive">
              <Trash2 className="h-4 w-4" />
            </Button>
          </AlertDialogTrigger>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>Are you sure?</AlertDialogTitle>
              <AlertDialogDescription>
                This will permanently delete the "{role.roleName}" role from this service. This action cannot be undone.
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <AlertDialogCancel>Cancel</AlertDialogCancel>
              <AlertDialogAction onClick={onDelete} className="bg-destructive hover:bg-destructive/90 text-destructive-foreground">Delete</AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
      </div>
    </div>
  );
}

export default function EventDetailsModal({ event: initialEvent, isOpen, onClose }: EventDetailsModalProps) {
  const { churchId, user: appUser } = useAuth();
  const { firestore } = useFirebase();
  const { toast } = useToast();
  const [isSaving, setIsSaving] = useState(false);
  
  const eventDocRef = useMemoFirebase(() => 
    firestore && churchId ? doc(firestore, 'churches', churchId, 'events', initialEvent.id) : null
  , [firestore, churchId, initialEvent.id]);
  const { data: currentEvent, isLoading: isLoadingEvent } = useDoc<Event>(eventDocRef);

  const volunteersQuery = useMemoFirebase(() => 
    firestore && churchId ? query(collection(firestore, 'users'), where('churchId', '==', churchId)) : null
  , [firestore, churchId]);
  const { data: volunteers = [] } = useCollection<Volunteer>(volunteersQuery);

  const rolesQuery = useMemoFirebase(() => 
    firestore && churchId ? query(collection(firestore, 'churches', churchId, 'universalRoles')) : null
  , [firestore, churchId]);
  const { data: universalRoles = [] } = useCollection<UniversalRole>(rolesQuery);

  const updateEvent = (updates: Partial<Event>) => {
    if (!eventDocRef) return;
    setIsSaving(true);
    const updatedData = { ...updates };
    updateDoc(eventDocRef, updatedData)
        .catch(async (serverError) => {
            const permissionError = new FirestorePermissionError(appUser, {
                path: eventDocRef.path,
                operation: 'update',
                requestResourceData: updatedData,
            });
            errorEmitter.emit('permission-error', permissionError);
        })
        .finally(() => {
            setIsSaving(false);
        });
  };

  const handleAddRole = (roleId: string) => {
    if (!currentEvent || !eventDocRef) return;
    const roleToAdd = universalRoles.find(r => r.id === roleId);
    if (!roleToAdd) return;
    
    const newRole: Role = {
      id: `role_${Date.now()}_${Math.random()}`,
      roleName: roleToAdd.name,
      assignedVolunteerId: null,
      assignedVolunteerName: null,
      status: 'Pending',
    };
    
    const updatedData = { roles: arrayUnion(newRole) };
    updateDoc(eventDocRef, updatedData)
        .then(() => {
            toast({ title: 'Role Added', description: `"${roleToAdd.name}" has been added.` });
        })
        .catch(async (serverError) => {
             const permissionError = new FirestorePermissionError(appUser, {
                path: eventDocRef.path,
                operation: 'update',
                requestResourceData: updatedData,
            });
            errorEmitter.emit('permission-error', permissionError);
        });
  };

  const handleUpdateRole = (roleId: string, roleName: string) => {
    if (!currentEvent) return;
    const currentRole = currentEvent.roles.find(r => r.id === roleId);
    if (!currentRole) return;
    const updatedRole = { ...currentRole, roleName };
    const newRoles = currentEvent.roles.map(r => r.id === roleId ? updatedRole : r);
    updateEvent({ roles: newRoles });
    toast({ title: 'Role Updated', description: `Role name changed to "${roleName}".` });
  };
  
  const handleDeleteRole = (roleId: string) => {
    if (!currentEvent || !eventDocRef) return;
    const roleToDelete = currentEvent.roles.find(r => r.id === roleId);
    if (!roleToDelete) return;
    
    const updates: any = { roles: arrayRemove(roleToDelete) };
    if (roleToDelete.assignedVolunteerId) {
      updates.assignedVolunteerIds = arrayRemove(roleToDelete.assignedVolunteerId);
    }

    updateDoc(eventDocRef, updates)
        .then(() => {
            toast({ title: 'Role Deleted' });
        })
        .catch(async (serverError) => {
            const permissionError = new FirestorePermissionError(appUser, {
                path: eventDocRef.path,
                operation: 'update',
                requestResourceData: updates,
            });
            errorEmitter.emit('permission-error', permissionError);
        });
  };

  const handleAssignVolunteer = (roleId: string, volunteerId: string | null) => {
    if (!currentEvent || !eventDocRef || !firestore) return;
    const volunteer = volunteers.find(v => v.id === volunteerId) || null;
    const roleToUpdate = currentEvent.roles.find(r => r.id === roleId);
    if (!roleToUpdate) return;
    
    const oldVolunteerId = roleToUpdate.assignedVolunteerId;

    roleToUpdate.assignedVolunteerId = volunteer?.id || null;
    roleToUpdate.assignedVolunteerName = volunteer?.name || null;
    roleToUpdate.status = volunteer ? 'Pending' : 'Pending';

    const newRoles = currentEvent.roles.map(r => r.id === roleId ? roleToUpdate : r);

    setIsSaving(true);
    const batch = writeBatch(firestore);
    
    batch.update(eventDocRef, { roles: newRoles });

    if (oldVolunteerId) {
        batch.update(eventDocRef, { assignedVolunteerIds: arrayRemove(oldVolunteerId) });
    }
    if (volunteerId && volunteerId !== 'unassign') {
         batch.update(eventDocRef, { assignedVolunteerIds: arrayUnion(volunteerId) });
    }

    batch.commit()
        .then(() => {
            toast({
                title: 'Assignment Updated',
                description: `Assigned ${volunteer ? volunteer.name : 'nobody'} to ${roleToUpdate.roleName}.`,
            });
        })
        .catch(async (serverError) => {
            console.error("Error assigning volunteer:", serverError);
            const permissionError = new FirestorePermissionError(appUser, {
                path: eventDocRef.path,
                operation: 'update',
                requestResourceData: { roles: newRoles, assignedVolunteerIds: '...' },
            });
            errorEmitter.emit('permission-error', permissionError);
        })
        .finally(() => {
            setIsSaving(false);
        });
  };
  
  const event = currentEvent || initialEvent;
  const isLoading = isLoadingEvent;

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-3xl">
        <DialogHeader>
          <DialogTitle className="font-headline text-2xl">{event.eventName}</DialogTitle>
          <div className="flex flex-wrap items-center gap-x-4 gap-y-1 text-sm text-muted-foreground pt-1">
            <span className="flex items-center gap-1.5"><Calendar className="h-4 w-4" /> {format(event.eventDate, 'EEEE, MMMM d, yyyy')}</span>
            <span className="flex items-center gap-1.5"><Clock className="h-4 w-4" /> {format(event.eventDate, 'p')}</span>
          </div>
        </DialogHeader>

        {isLoading ? (
            <div className="flex justify-center items-center h-64">
                <Loader2 className="h-8 w-8 animate-spin" />
            </div>
        ) : (
        <div className="py-4 space-y-6">
          <Card>
            <CardContent className="p-4 space-y-2">
              <h3 className="font-semibold mb-2">Volunteer Roles</h3>
              {event.roles.length > 0 ? (
                event.roles.map(role => (
                  <RoleRow
                    key={role.id}
                    role={role}
                    onUpdate={(newName) => handleUpdateRole(role.id, newName)}
                    onDelete={() => handleDeleteRole(role.id)}
                    onAssign={(volId) => handleAssignVolunteer(role.id, volId === 'unassign' ? null : volId)}
                    volunteers={volunteers}
                    isSaving={isSaving}
                  />
                ))
              ) : (
                <p className="text-sm text-muted-foreground text-center p-4">No roles for this service yet.</p>
              )}
            </CardContent>
          </Card>
          
          <div className="grid grid-cols-2 gap-4">
              <Select onValueChange={handleAddRole} value="">
                <SelectTrigger disabled={isSaving}>
                    <SelectValue placeholder="Add a role..." />
                </SelectTrigger>
                <SelectContent>
                    {universalRoles.map(role => (
                        <SelectItem key={role.id} value={role.id}>{role.name}</SelectItem>
                    ))}
                </SelectContent>
              </Select>
            <Button variant="outline" disabled={true}>
                <Sparkles className="mr-2 h-4 w-4" /> Auto-Assign All
            </Button>
          </div>
        </div>
        )}

        <DialogFooter>
          <Button variant="outline" onClick={onClose}>Close</Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}
