
'use client';
import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Plus, Upload, Edit, User, Loader2 } from 'lucide-react';
import EventCalendar from '@/components/admin/EventCalendar';
import CreateEventModal from '@/components/admin/CreateEventModal';
import CreateTemplateModal from '@/components/admin/CreateTemplateModal';
import EventDetailsModal from '@/components/admin/EventDetailsModal';
import { Header } from '@/components/layout/Header';
import type { Event } from '@/types';
import ImportIcsModal from '@/components/admin/ImportIcsModal';
import { parseIcsContent } from '@/ai/flows/parse-ics-flow';
import { useRouter } from 'next/navigation';
import AppShell from '@/components/layout/AppShell';
import { format, isSameDay } from 'date-fns';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';
import { collection, doc, query, writeBatch, where, getDoc, addDoc } from 'firebase/firestore';
import { useToast } from '@/hooks/use-toast';
import { useAuth } from '@/hooks/use-auth';
import { useFirebase, useMemoFirebase } from '@/firebase';
import { useCollection } from '@/firebase/hooks/use-collection';
import { errorEmitter } from '@/firebase/error-emitter';
import { FirestorePermissionError } from '@/firebase/errors';

export default function ServicesPage() {
  const { churchId, isLoggedIn, isAdmin, user: appUser, isUserLoading } = useAuth();
  const { firestore } = useFirebase();
  const [selectedEvent, setSelectedEvent] = useState<Event | null>(null);
  const [isCreateEventModalOpen, setIsCreateEventModalOpen] = useState(false);
  const [isCreateTemplateModalOpen, setIsCreateTemplateModalOpen] = useState(false);
  const [isEventDetailsModalOpen, setIsEventDetailsModalOpen] = useState(false);
  const [isImportIcsModalOpen, setIsImportIcsModalOpen] = useState(false);
  const [selectedDate, setSelectedDate] = useState<Date>(new Date());
  const router = useRouter();
  const { toast } = useToast();
  
  const eventsQuery = useMemoFirebase(() => {
      if (!firestore || !churchId) return null;
      return query(collection(firestore, 'churches', churchId, 'events'));
  }, [firestore, churchId]);

  const { data: events, isLoading: isLoadingEvents } = useCollection<Event>(eventsQuery);

  useEffect(() => {
      if (isUserLoading) return;
      if (!isLoggedIn) {
          router.push('/');
      } else if (!isAdmin) {
          router.push('/my-schedule');
      }
  }, [isLoggedIn, isAdmin, router, isUserLoading]);

  const handleDateSelect = (date: Date) => {
      setSelectedDate(date);
  };

  const onEventClick = (event: Event) => {
      setSelectedEvent(event);
      setIsEventDetailsModalOpen(true);
  };

  const handleCreateEvent = async (eventData: Omit<Event, 'id' | 'roles' | 'assignedVolunteerIds' | 'churchId'>, templateId?: string): Promise<Event | undefined> => {
    if (!firestore || !churchId) {
        toast({
            variant: "destructive",
            title: "Error",
            description: "You must be logged in to create an event."
        });
        return;
    };
    
    let roles: Event['roles'] = [];
    
    if (templateId) {
        try {
            const templateRef = doc(firestore, 'churches', churchId, 'serviceTemplates', templateId);
            const templateSnap = await getDoc(templateRef);
            if (templateSnap.exists()) {
                const templateData = templateSnap.data();
                roles = templateData.roles.map((role: any) => ({
                    id: `role_${Date.now()}_${Math.random()}`,
                    roleName: role.name,
                    assignedVolunteerId: null,
                    assignedVolunteerName: null,
                    status: 'Pending'
                }));
            }
        } catch (e) {
             console.error("Error fetching template: ", e);
             toast({ variant: "destructive", title: "Error applying template" });
        }
    }
    
    const newEventData: Omit<Event, 'id'> = {
        ...eventData,
        churchId,
        roles,
        assignedVolunteerIds: [], // Start with empty, will be populated on assignment
    };

    const eventsCollectionRef = collection(firestore, 'churches', churchId, 'events');
    const newDocRefPromise = addDoc(eventsCollectionRef, newEventData);

    newDocRefPromise.catch(async (serverError) => {
        console.error("Error creating event: ", serverError);
        const permissionError = new FirestorePermissionError(appUser, {
            path: eventsCollectionRef.path,
            operation: 'create',
            requestResourceData: newEventData
        });
        errorEmitter.emit('permission-error', permissionError);
    });

    const newDocRef = await newDocRefPromise;
    return { id: newDocRef.id, ...newEventData };
  }


  const handleUploadIcs = async (icsContent: string) => {
    try {
      const parsedEvents = await parseIcsContent(icsContent);

      if (parsedEvents && parsedEvents.length > 0 && firestore && churchId) {
        for (const event of parsedEvents) {
            await handleCreateEvent({
                eventName: event.eventName,
                eventDate: new Date(event.eventDate),
                notes: event.notes,
            });
        }
        toast({
            title: "Import Successful",
            description: `${parsedEvents.length} events imported successfully!`,
        });
      } else {
        toast({
          title: "No Events Found",
          description: "No events were found in the ICS file.",
          variant: "destructive",
        });
      }

    } catch (error: any) {
      console.error("Error importing ICS:", error);
      toast({
        title: "Import Failed",
        description: error.message || "Failed to import events from ICS file.",
        variant: "destructive",
      });
    } finally {
      setIsImportIcsModalOpen(false);
    }
  };
  
  const isLoading = isUserLoading || isLoadingEvents;

  if (isLoading) {
    return (
        <div className="flex min-h-screen w-full flex-col items-center justify-center bg-background">
            <Loader2 className="h-8 w-8 animate-spin text-primary" />
            <p className="text-muted-foreground mt-2">Loading Services...</p>
        </div>
    );
  }

  const filteredEvents = events ? events.filter(event => isSameDay(event.eventDate, selectedDate)) : [];

  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex-1 p-4 md:p-8">
            <div className="flex items-center justify-between mb-6">
              <h1 className="text-3xl font-bold font-headline tracking-tight">
                Services
              </h1>
              <div className="flex items-center gap-2">
                <Button onClick={() => setIsImportIcsModalOpen(true)}>
                    <Upload className="mr-2 h-4 w-4"/> Import
                </Button>
                 <Button onClick={() => setIsCreateEventModalOpen(true)}>
                    <Plus className="mr-2 h-4 w-4"/> Create Service
                </Button>
              </div>
            </div>
            
            <div className="grid md:grid-cols-3 gap-6">
                <div className="md:col-span-2">
                    <EventCalendar events={events || []} onDayClick={handleDateSelect} selectedDay={selectedDate} />
                </div>
                <div className="md:col-span-1">
                     <h2 className="text-xl font-bold mb-4">
                        {format(selectedDate, 'MMMM d, yyyy')}
                    </h2>
                     {filteredEvents.length > 0 ? (
                        <Accordion type="single" collapsible defaultValue="item-0">
                            {filteredEvents.map((event, index) => (
                                <AccordionItem key={event.id} value={`item-${index}`}>
                                    <AccordionTrigger>
                                        <div className="flex flex-col items-start text-left">
                                            <span className="font-semibold">{event.eventName}</span>
                                            <span className="text-sm text-muted-foreground">{format(event.eventDate, 'p')}</span>
                                        </div>
                                    </AccordionTrigger>
                                    <AccordionContent>
                                        <div className="space-y-2">
                                            {event.roles.map(role => (
                                                <div key={role.id} className="flex justify-between items-center text-sm p-2 rounded-md bg-muted/50">
                                                    <span>{role.roleName}</span>
                                                    <span className="flex items-center gap-2 text-muted-foreground">
                                                        <User className="h-4 w-4" />
                                                        {role.assignedVolunteerName || 'Unassigned'}
                                                    </span>
                                                </div>
                                            ))}
                                            {event.roles.length === 0 && <p className="text-sm text-muted-foreground">No roles for this service.</p>}
                                        </div>
                                         <Button size="sm" className="mt-4 w-full" onClick={() => onEventClick(event)}>
                                            <Edit className="mr-2 h-4 w-4"/> Manage Service
                                        </Button>
                                    </AccordionContent>
                                </AccordionItem>
                            ))}
                        </Accordion>
                     ) : (
                        <div className="text-center py-10 border-2 border-dashed rounded-lg">
                            <p className="text-muted-foreground">No services scheduled for this day.</p>
                        </div>
                     )}
                </div>
            </div>
        </main>
        
        <CreateEventModal 
            isOpen={isCreateEventModalOpen}
            onClose={() => setIsCreateEventModalOpen(false)}
            onCreateTemplate={() => {
                setIsCreateEventModalOpen(false);
                setIsCreateTemplateModalOpen(true);
            }}
            createEvent={handleCreateEvent}
        />
        
        <CreateTemplateModal 
            isOpen={isCreateTemplateModalOpen} 
            onClose={() => setIsCreateTemplateModalOpen(false)}
        />
        
        {selectedEvent && (
             <EventDetailsModal 
                event={selectedEvent}
                isOpen={isEventDetailsModalOpen}
                onClose={() => setIsEventDetailsModalOpen(false)}
            />
        )}
        <ImportIcsModal 
            isOpen={isImportIcsModalOpen}
            onClose={() => setIsImportIcsModalOpen(false)}
            importAction={parseIcsContent}
            createEvent={handleCreateEvent}
        />
      </div>
    </AppShell>
  );
}
